.stock-vector-machine-learning-icon {
  height: 205px;
  width: 341px;
  position: relative;
  object-fit: cover;
  display: none;
  max-width: 100%;
  z-index: 0;
}
.models-for-image {
  height: 54px;
  width: 174px;
  position: relative;
  display: inline-block;
  flex-shrink: 0;
  z-index: 1;
}
.social-icon-loading-dribbble-1 {
  width: 207px;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  bottom: 0;
  left: 0;
  max-height: 100%;
  object-fit: cover;
  mix-blend-mode: darken;
  z-index: 1;
}
.vega-virtual {
  width: 208px;
  position: relative;
  display: inline-block;
  z-index: 2;
}
.image-analysis-frame,
.n-a-s-a-space-game-frame {
  display: flex;
  align-items: flex-end;
  box-sizing: border-box;
}
.image-analysis-frame {
  height: 166px;
  width: 218px;
  margin: 0 !important;
  position: absolute;
  top: -5px;
  right: -216px;
  flex-direction: column;
  justify-content: flex-end;
  padding: var(--padding-mid) 0;
  font-family: var(--font-petrona);
}
.n-a-s-a-space-game-frame {
  height: 205px;
  width: 341px;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl) 0 var(--padding-2xl);
  position: relative;
  background-image: url(/public/stockvectormachinelearninglogoneuronsconnectionssynapsesemblemneuralnetworkisolatedhumanbrain1743390782-1@2x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  max-width: 100%;
}
.icon-256x256-1 {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: contain;
  mix-blend-mode: normal;
}
.hotel-reservation-system1 {
  width: 116px;
  position: relative;
  display: inline-block;
}
.object-other-instance,
.s-h-a-p-analysis-frame {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.s-h-a-p-analysis-frame {
  align-self: stretch;
  flex: 1;
  align-items: flex-end;
  gap: 21px 0;
}
.object-other-instance {
  height: 171px;
  width: 116px;
  align-items: flex-start;
  padding: var(--padding-lg) 0 0;
  box-sizing: border-box;
  min-width: 116px;
}
.objectother-07-icon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.data-analysis {
  position: relative;
}
.data-frame {
  height: 149px;
  width: 140px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 34px 0;
  min-width: 140px;
}
.data-analysis-frame {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.hotel-reservation-system-frame {
  width: 347px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs) 0 0;
  box-sizing: border-box;
  max-width: 100%;
  font-family: var(--font-petrona);
}
.about-me-frame,
.v-frame {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.v-frame {
  flex: 1;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.about-me-frame {
  align-self: stretch;
  justify-content: flex-start;
  padding: 0 var(--padding-9xl) 0 0;
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-xs);
  color: var(--color-black);
  font-family: var(--font-philosopher);
}
@media screen and (max-width: 1050px) {
  .v-frame {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 450px) {
  .data-frame,
  .object-other-instance {
    flex: 1;
  }
  .data-analysis-frame {
    flex-wrap: wrap;
    justify-content: center;
  }
}
