.frame-parent {
  width: 1007px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 4px 0;
  max-width: 100%;
}
.frame-group {
  width: 1048px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  max-width: 100%;
  flex-shrink: 0;
}
.about-section {
  width: 1018px;
  flex-direction: column;
  align-items: flex-end;
  gap: 63px 0;
  max-width: 100%;
}
.about-section,
.desktop-1,
.frame-with-projects {
  display: flex;
  justify-content: flex-start;
}
.frame-with-projects {
  width: 1064px;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
  flex-shrink: 0;
}
.desktop-1 {
  width: 100%;
  position: relative;
  background-color: #fff;
  overflow: hidden;
  flex-direction: column;
  align-items: center;
  padding: 73px 74px 2876px var(--padding-xl);
  box-sizing: border-box;
  gap: 204px 0;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .desktop-1 {
    gap: 204px 0;
    padding-right: 37px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .about-section {
    gap: 63px 0;
  }
}
@media screen and (max-width: 750px) {
  .about-section {
    gap: 63px 0;
  }
  .desktop-1 {
    gap: 204px 0;
  }
}
@media screen and (max-width: 450px) {
  .desktop-1 {
    gap: 204px 0;
  }
}
