.about1 {
  margin: 0;
  width: 517px;
  height: 68px;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  display: inline-block;
  flex-shrink: 0;
  max-width: 100%;
}
.wrapper-ellipse-1,
.wrapper-ellipse-1-child {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.wrapper-ellipse-1-child {
  object-fit: contain;
  left: 0;
  transform: scale(1.764);
}
.wrapper-ellipse-1 {
  left: -0.5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.maxresdefault-1-icon {
  position: absolute;
  top: 214px;
  left: 324px;
  width: 0;
  height: 0;
  object-fit: cover;
  z-index: 1;
}
.vector-ellipse {
  position: absolute;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  width: 542px;
}
.maxresdefault-2-icon {
  position: absolute;
  top: 4px;
  left: 12px;
  border-radius: var(--br-19xl);
  width: 562px;
  height: 285px;
  object-fit: cover;
  z-index: 2;
}
.machine-learning1 {
  height: 343px;
  flex: 1;
  position: relative;
  min-width: 373px;
  max-width: 100%;
}
.im-a-student {
  width: 377px;
  height: 356px;
  position: relative;
  display: inline-block;
  flex-shrink: 0;
}
.text-intro {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-17xl) 0 0;
  box-sizing: border-box;
  min-width: 377px;
  max-width: 100%;
}
.ml-student-dsu {
  width: 1005px;
  flex-direction: row;
  align-items: flex-start;
  gap: 0 54px;
}
.experience-section,
.its-me-darshan,
.ml-student-dsu {
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.its-me-darshan {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  font-size: var(--font-size-mini);
  font-family: var(--font-ovo);
}
.experience-section {
  width: 1132px;
  flex-direction: column;
  align-items: flex-end;
  padding: 0 0 var(--padding-32xl);
  box-sizing: border-box;
  gap: 34px 0;
  flex-shrink: 0;
  text-align: left;
  font-size: var(--font-size-31xl);
  color: var(--color-black);
  font-family: var(--font-oregano);
}
@media screen and (max-width: 1050px) {
  .about1 {
    font-size: var(--font-size-21xl);
  }
  .text-intro {
    flex: 1;
  }
  .ml-student-dsu {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .machine-learning1,
  .text-intro {
    min-width: 100%;
  }
  .ml-student-dsu {
    gap: 0 54px;
  }
  .experience-section {
    gap: 34px 0;
    padding-bottom: var(--padding-14xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .about1 {
    font-size: var(--font-size-11xl);
  }
}
