}
.cse-aiml {
  margin: 0;
}
.cse-aiml-container {
  flex: 1;
  position: relative;
}
.about-section1,
.welcome-text {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.about-section1 {
  width: 221px;
  flex-direction: row;
  padding: 0 var(--padding-10xs);
  box-sizing: border-box;
  font-size: var(--font-size-mini);
}
.welcome-text {
  align-self: stretch;
  flex-direction: column;
  gap: 3px 0;
}
.machine-learning {
  margin: 0;
}
.devops-open {
  margin: 0;
  white-space: pre-wrap;
}
.machine-learning-container {
  flex: 1;
  position: relative;
  display: inline-block;
  max-width: 100%;
}
.image-analysis {
  width: 439px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-11xs);
  box-sizing: border-box;
  max-width: 100%;
  font-size: var(--font-size-mini);
  font-family: var(--font-overlock);
}
.hi-and-info-frame,
.hi-and-info-frame-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.hi-and-info-frame {
  width: 517px;
  gap: 64px 0;
  max-width: 104%;
  flex-shrink: 0;
}
.hi-and-info-frame-wrapper {
  flex: 1;
  padding: var(--padding-xl) 0 var(--padding-xl) 0;
  box-sizing: border-box;
  min-width: 324px;
  min-height: 254px;
  max-width: 100%;
}
.file-icon {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  background-color: var(--color-firebrick);
  filter: blur(131px);
  width: 100%;
  height: 100%;
}
.file-1-icon {
  position: absolute;
  height: calc(100% - 19px);
  top: 10px;
  bottom: 9px;
  left: 8px;
  border-radius: var(--br-177xl);
  max-height: 100%;
  width: 280px;
  object-fit: cover;
  z-index: 1;
}
.maxresdefault-label {
  height: 308px;
  width: 288px;
  position: relative;
}
.frame-container,
.frame-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  max-width: 100%;
}
.frame-container {
  flex: 1;
  align-items: center;
  row-gap: 20px;
}
.frame-wrapper {
  width: 973px;
  align-items: flex-start;
  padding: 0 var(--padding-10xs);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-31xl);
  color: var(--color-black);
  font-family: var(--font-outfit);
}
@media screen and (max-width: 1050px) {
  .hi-im-darshan {
    font-size: var(--font-size-21xl);
  }
  .hi-and-info-frame-wrapper {
    min-height: auto;
  }
  .frame-container {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .hi-and-info-frame {
    gap: 64px 0;
  }
}
@media screen and (max-width: 450px) {
  .hi-im-darshan {
    font-size: var(--font-size-11xl);
  }
  .hi-and-info-frame {
    gap: 64px 0;
  }
}
