.projects1 {
  margin: 0;
  height: 68px;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.projects-list {
  width: 619px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-32xl);
  box-sizing: border-box;
  max-width: 100%;
}
.image-analysis-and-object-dete-icon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.real-time-object {
  width: 105px;
  position: relative;
  display: inline-block;
  flex-shrink: 0;
}
.morse-code-translator {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-xl) 0 var(--padding-8xl);
}
.dev-ops-implementation,
.stock-photo-icon {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.dev-ops-implementation {
  align-self: stretch;
  height: 170px;
  align-items: center;
  gap: 1px 0;
}
.stock-photo-icon {
  height: 183px;
  width: 185px;
  align-items: flex-start;
  padding: 0 var(--padding-2xl) 0 0;
  box-sizing: border-box;
  font-family: var(--font-petrona);
}
.istockphoto-1335265790-612x612-icon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.implementation-of-8 {
  width: 136px;
  position: relative;
  display: inline-block;
  flex-shrink: 0;
}
.hotel-reservation-system,
.social-media-icon {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-xl) 0 var(--padding-11xl);
}
.social-media-icon {
  height: 162px;
  width: 219px;
  flex-direction: column;
  align-items: center;
  padding: 0 var(--padding-6xs) 0 0;
  box-sizing: border-box;
  gap: 5px 0;
}
.v040-14597-1-icon {
  width: 128px;
  flex: 1;
  position: relative;
  max-height: 100%;
  object-fit: contain;
}
.nasa-space-game {
  height: 25px;
  flex: 1;
  position: relative;
  display: inline-block;
}
.data-analysis-s-h-a-p,
.empty-frame,
.separator-line {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.separator-line {
  align-self: stretch;
  flex-direction: row;
  padding: 0 0 0 var(--padding-sm);
}
.data-analysis-s-h-a-p,
.empty-frame {
  flex-direction: column;
}
.empty-frame {
  align-self: stretch;
  flex: 1;
  gap: 18px 0;
}
.data-analysis-s-h-a-p {
  height: 162px;
  width: 146px;
  padding: 0 0 var(--padding-mini);
  box-sizing: border-box;
}
.morse-code-display {
  width: 43px;
  height: 45px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  left: 24px;
  object-fit: contain;
}
.header-info,
.morse-code-translator1 {
  align-self: stretch;
  position: relative;
}
.header-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding: var(--padding-44xl) 0 0;
}
.morse-code-key-board {
  height: 129px;
  width: 165px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.real-time-detection {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
  font-size: var(--font-size-xs);
}
.projects-list-parent {
  width: 949px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 10px 0;
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-31xl);
  color: var(--color-black);
  font-family: var(--font-philosopher);
}
@media screen and (max-width: 1050px) {
  .projects1 {
    font-size: var(--font-size-21xl);
  }
  .real-time-detection {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .projects-list {
    padding-left: var(--padding-6xl);
    padding-right: var(--padding-6xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .projects1 {
    font-size: var(--font-size-11xl);
  }
}
