.d-letter-hand-drawn-symbol-vec-icon {
  height: 97px;
  width: 112px;
  position: relative;
  border-radius: var(--br-21xl) var(--br-21xl) var(--br-xl) var(--br-21xl);
  object-fit: contain;
  mix-blend-mode: normal;
}
.about,
.contact,
.experience,
.hackathon,
.home,
.projects,
.skills {
  height: 133.4px;
  width: 75.6px;
  position: relative;
  line-height: 27.5px;
  display: inline-block;
  flex-shrink: 0;
  transform: rotate(-1.18deg);
}
.about,
.contact,
.experience,
.hackathon,
.projects,
.skills {
  height: 64.7px;
  width: 41.4px;
  transform: rotate(-1.13deg);
}
.contact,
.experience,
.hackathon,
.projects,
.skills {
  width: 32.3px;
}
.contact,
.experience,
.hackathon,
.projects {
  width: 54.1px;
  line-height: 18px;
}
.contact,
.experience,
.hackathon {
  width: 74.4px;
}
.contact,
.hackathon {
  width: 73.7px;
}
.contact {
  width: 57.5px;
}
.f-r-a-m-e,
.rectangle {
  display: flex;
  align-items: flex-start;
}
.f-r-a-m-e {
  margin: 0;
  align-self: stretch;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 var(--padding-7xs) 0 0;
  gap: var(--gap-xl);
  white-space: nowrap;
  text-align: left;
  font-size: var(--font-size-xs);
  color: var(--color-black);
  font-family: var(--font-outfit);
}
.rectangle {
  width: 710px;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-36xl) var(--padding-12xs) 0 0;
  box-sizing: border-box;
  max-width: 100%;
}
.d-letter-hand-drawn-symbol-vec-parent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 100%;
  gap: var(--gap-xl);
}
@media screen and (max-width: 1050px) {
  .d-letter-hand-drawn-symbol-vec-parent {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .f-r-a-m-e {
    flex-wrap: wrap;
  }
}
